import {useState} from "react";
import './index.css';
import {Button, Container, Grid, Paper, Typography} from "@material-ui/core";
import BSCTokens from "./components/BSCTokens";
import ETHTokens from "./components/ETHTokens";


function App() {

  const [currentAccount, setCurrentAccount] = useState(null);

  const [chainId, setChainId] = useState(window.ethereum.chainId);
  const [connected, setConnected] = useState(window.ethereum.isConnected())

  const erc20 = '0x1';
  const bep20 = '0x38';

  window.ethereum.on('chainChanged', (chainId) => {
    setChainId(chainId)
  });

  window.ethereum.on('connect', () => {
    setConnected(true);

    window.ethereum.request({ method: 'eth_chainId' }).then( resp => {
      setChainId(resp);
    })
  });


  if (!window.ethereum.isMetaMask) {
    return <div>MetaMask not detected</div>
  }

  if(!connected) {
    return <Button variant="contained" onClick={connectWallet}>Connect Wallet</Button>
  }

  function connectWallet(){
      window.ethereum
        .request({ method: 'eth_requestAccounts' })
        .then(handleAccountsChanged)
        .catch((err) => {
          if (err.code === 4001) {
            // EIP-1193 userRejectedRequest error
            // If this happens, the user rejected the connection request.
            console.log('Please connect to MetaMask.');
          } else {
            console.error(err);
          }
        });
  }

  function handleAccountsChanged(accounts) {
    if (accounts.length === 0) {
      // MetaMask is locked or the user has not connected any accounts
      console.log('Please connect to MetaMask.');
    } else if (accounts[0] !== currentAccount) {
      setCurrentAccount(accounts[0]);
      // Do any other work!
    }
  }

  return (
    <div className="App">
      <Container>
        <Grid container spacing={2} style={{marginTop: 40}}>
          {chainId == bep20 ? <BSCTokens/> : null}
          {chainId == erc20 ? <ETHTokens/> : null}
        </Grid>

        <Grid container spacing={2} style={{marginTop: 40}}>
          <Grid item xs={12}>
            <Paper style={{padding: 20}}>
              <Typography variant="body1">If you found this tool helpful... BSC: 0xE9F591CC265fbE7225DD3d260A7a98C120EB8ef1 🙏 🦍</Typography>
            </Paper>
          </Grid>
        </Grid>
      </Container>
    </div>
  );

}

export default App;

export function copy2clip(value, scope) {
  //create temp element, assign value, copy
  let div = document.createElement('input');
  document.body.append(div)
  div.value = value;
  div.select();
  div.setSelectionRange(0, 99999)
  document.execCommand("copy");

  //show notice
  // scope.setState({clipboardOpen: true});

  //remove element
  document.body.removeChild(div);
}

