import {
  Card,
  CardActionArea,
  CardActions,
  CardContent,
  CardMedia, Grid,
  IconButton, Snackbar, Tooltip,
  Typography
} from "@material-ui/core";
import AddIcon from '@material-ui/icons/Add';
import SearchIcon from '@material-ui/icons/Search';
import FileCopyIcon from '@material-ui/icons/FileCopy';
import Alert from '@material-ui/lab/Alert';
import {useState} from "react";

function MetaMaskButton(props) {
  const {address, symbol, type, deci, explorer} = props;
  const [open, setOpen] = useState(false);

  async function addToken() {

    if (!window.ethereum) {
      return;
    }

    try {
      // wasAdded is a boolean. Like any RPC method, an error may be thrown.
      const wasAdded = await window.ethereum.request({
        method: 'wallet_watchAsset',
        params: {
          type: type,
          options: {
            address: address,
            symbol: symbol,
            decimals: deci,
            image: `https://feg.army/metamask/logos/${symbol}.png`,
          },
        },
      });

      if (wasAdded) {
        console.log('Thanks for your interest!');
      } else {
        console.log('Your loss!');
      }
    } catch (error) {
      console.log(error);
    }
  }

  return (
    <Grid item xs={12} sm={6} md={4} lg={3}>
      <Card style={{maxWidth: 320}}>
        <CardActionArea>
          <CardMedia
            image={`http://feg.army/metamask/logos/${symbol}.png`}
            title={symbol}
            style={{height: 128}}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              {symbol}
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions>
          <Tooltip title="Add To MetaMask">
            <IconButton size="small" color="primary" onClick={addToken}>
              <AddIcon/>
            </IconButton>
          </Tooltip>
          <Tooltip title="View on Scanner" onClick={()=> {
            window.open(`${explorer}/${address}`);
          }}>
            <IconButton size="small" color="primary">
              <SearchIcon/>
            </IconButton>
          </Tooltip>
          <Tooltip title="Copy Contract Address" onClick={() => {
            copy2clip(address);
            setOpen(true)
          }}>
            <IconButton size="small" color="primary">
              <FileCopyIcon/>
            </IconButton>
          </Tooltip>
        </CardActions>
      </Card>
      <Snackbar open={open} autoHideDuration={6000} onClose={()=>{setOpen(false)}}>
        <Alert severity="success">
          Copied to clipboard
        </Alert>
      </Snackbar>
    </Grid>

  )
}

export {MetaMaskButton};

export function copy2clip(value) {
  //create temp element, assign value, copy
  let div = document.createElement('input');
  document.body.append(div)
  div.value = value;
  div.select();
  div.setSelectionRange(0, 99999)
  document.execCommand("copy");

  //remove element
  document.body.removeChild(div);
}

